.wrapper {
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  .hireBLock {
    padding: 10px;
    height: 100px;
    min-width: 40%;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;

    .button {
      min-width: 300px;
      min-height: 60px;
      font-family: 'OpenSans', sans-serif;
      font-size: 22px;
      text-transform: uppercase;
      letter-spacing: 1.3px;
      font-weight: 700;
      color: #FFFFFF;
      background: #4FD1C5;
      background: linear-gradient(90deg, #ffb400 0%, #ffb400 100%);
      border: none;
      border-radius: 1000px;
      box-shadow: 12px 12px 24px rgba(79, 209, 197, .64);
      transition: all 0.3s ease-in-out 0s;
      cursor: pointer;
      outline: none;
      position: relative;
      padding: 10px;

      &:before {
        content: '';
        border-radius: 1000px;
        min-width: calc(300px + 12px);
        min-height: calc(60px + 12px);
        border: 6px solid #00FFCB;
        box-shadow: 0 0 60px rgba(0, 255, 203, .64);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: all .3s ease-in-out 0s;
      }

      &:focus {
        color: #313133;
        transform: translateY(-6px);

        &:after {
          animation: none;
          display: none;
        }
      }

      &:hover {
        color: #313133;
        transform: translateY(-6px);

        &:after {
          animation: none;
          display: none;
        }
      }

      &:hover:before {
        opacity: 1;
      }

      &:focus:before {
        opacity: 1;
      }

      &:after {
        content: '';
        width: 30px;
        height: 30px;
        border-radius: 100%;
        border: 6px solid #ffb501;
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation: ring 1.5s infinite;
      }
    }


  }

  @keyframes ring {
    0% {
      width: 30px;
      height: 30px;
      opacity: 1;
    }
    100% {
      width: 300px;
      height: 300px;
      opacity: 0;
    }
  }

}